import React from 'react';
import { Line } from 'react-chartjs-2';
import styled from 'styled-components';
import { Chart, LineElement, CategoryScale, LinearScale, PointElement, Title, Tooltip } from 'chart.js';

Chart.register(LineElement, CategoryScale, LinearScale, PointElement, Title, Tooltip);

const GraphContainer = styled.div`
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
`;

function StockGraph({ data }) {
  const dates = data.map(item => item.date);
  const prices = data.map(item => item.clpr);
  const priceDifferences = calculatePriceDifferences(prices); //   

  const chartData = {
    labels: dates,
    datasets: [
      {
        label: '',
        data: prices,
        fill: false,
        backgroundColor: 'rgba(75,192,192,1)',
        borderColor: 'rgba(75,192,192,1)',
        pointRadius: 5,
        pointHoverRadius: 8,
        pointHoverBackgroundColor: 'rgba(75,192,192,1)',
        pointHoverBorderColor: 'rgba(75,192,192,1)',
        pointHoverBorderWidth: 2,
        pointHoverDisplay: true,
      }
    ]
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      x: {
        title: {
          display: true,
          text: '',
        }
      },
      y: {
        title: {
          display: true,
          text: ' ()',
        },
        ticks: {
          callback: function(value) {
            return value.toLocaleString();
          }
        }
      }
    },
    plugins: {
      tooltip: {
        callbacks: {
          label: function(context) {
            let label = context.dataset.label || '';
            if (label) {
              label += ': ';
            }
            if (context.parsed.y !== null) {
              label += context.parsed.y.toLocaleString() + ' ';
            }

            //    
            if (priceDifferences[context.dataIndex]) {
              const percentChange = priceDifferences[context.dataIndex];
              label += ` (${percentChange >= 0 ? '+' : ''}${percentChange.toFixed(2)}%)`;
            }

            return label;
          }
        }
      }
    }
  };

  return (
    <GraphContainer>
      <Line data={chartData} options={options} />
    </GraphContainer>
  );
}

//    
const calculatePriceDifferences = (prices) => {
  const differences = [];
  for (let i = 0; i < prices.length; i++) {
    if (i === 0) {
      differences.push(0); //     
    } else {
      const percentChange = ((prices[i] - prices[i - 1]) / prices[i - 1]) * 100;
      differences.push(percentChange); //   
    }
  }
  return differences;
};

export default StockGraph;
