// App.js
import React, { useState } from 'react';
import SearchBar from './SearchBar';
import StockInfo from './StockInfo';
import StockGraph from './StockGraph'; //   
import axios from 'axios';
import convert from 'xml-js';
import styled, { ThemeProvider } from 'styled-components';
import { createGlobalStyle } from 'styled-components';

//    
const lightTheme = {
  background: '#f0f0f0',
  color: '#000',
  titleBg: '#0af07a',
  titleColor: '#000',
  buttonBg: '#228be6',
  buttonHoverBg: '#339af0',
  buttonActiveBg: '#1c7ed6',
};

const darkTheme = {
  background: '#333',
  color: '#fff',
  titleBg: '#0af07a',
  titleColor: '#fff',
  buttonBg: '#444',
  buttonHoverBg: '#555',
  buttonActiveBg: '#666',
};

const GlobalStyle = createGlobalStyle`
  body {
    background-color: ${({ theme }) => theme.background};
    color: ${({ theme }) => theme.color};
    transition: background-color 0.3s, color 0.3s;
  }
`;

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const Title = styled.h1`
  background-color: ${({ theme }) => theme.titleBg};
  color: ${({ theme }) => theme.titleColor};
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
`;

const ToggleButton = styled.button`
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 10px;
  background-color: ${({ theme }) => theme.buttonBg};
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  &:hover {
    background-color: ${({ theme }) => theme.buttonHoverBg};
  }
  &:active {
    background-color: ${({ theme }) => theme.buttonActiveBg};
  }
`;

function App() {
  const [stockData, setStockData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [graphData, setGraphData] = useState([]); //     
  const [isDarkMode, setIsDarkMode] = useState(false); //  

  const fetchStockData = async (itmsNm, startDate, endDate) => {
    setLoading(true);
    setError(null);
    setStockData(null);
    setGraphData([]);

    const API_KEY = 'CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD+chVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ=='; 
    const API_URL = `https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo`;

    try {
      let date = new Date(startDate);
      const endDateObj = new Date(endDate);
      const stockDataList = [];

      while (date <= endDateObj) {
        const formattedDate = date.toISOString().split('T')[0].replace(/-/g, '');
        console.log(` : ${formattedDate}`); 

        const response = await axios.get(API_URL, {
          params: { 
            serviceKey: API_KEY,
            itmsNm: itmsNm,
            basDt: formattedDate
          }
        });

        console.log('API :', response.data); 

        const jsonResult = convert.xml2js(response.data, { compact: true, spaces: 2 });
        console.log(' JSON:', jsonResult); // : JSON   

        const items = jsonResult.response.body.items?.item;

        if (items) {
          const itemArray = Array.isArray(items) ? items : [items];
          const stockItem = itemArray.find(item => item.itmsNm._text.toLowerCase() === itmsNm.toLowerCase());

          if (stockItem) {
            console.log(stockItem);
            stockDataList.push({
              date: formattedDate,
              itmsNm: stockItem.itmsNm?._text || '', //    
            mrktCtg: stockItem.mrktCtg?._text || '', //    
            clpr: parseFloat(stockItem.clpr?._text.replace(/,/g, '')) || 0, //     
            fltRt: parseFloat(stockItem.fltRt?._text) || 0,
            basDt: formattedDate
            }
          );
          }
        }

        date.setDate(date.getDate() + 1); //   
      }

      if (stockDataList.length > 0) {
        console.log('  :', stockDataList); // :    
        setGraphData(stockDataList); //   
        setStockData(stockDataList[stockDataList.length - 1]); //    
      } else {
        console.log('      .'); // :   
        setGraphData([]);
        setStockData(null);
        setError('      .');
      }
    } catch (error) {
      console.error('API  :', error); // : API   
      setError('  .');
    } finally {
      setLoading(false);
    }
  };

  const toggleTheme = () => {
    setIsDarkMode(!isDarkMode);
  };

  return (
    <ThemeProvider theme={isDarkMode ? darkTheme : lightTheme}>
      <GlobalStyle />
      <AppContainer>
        <Title>   </Title>
        <ToggleButton onClick={toggleTheme}>
          {isDarkMode ? ' ' : ' '}
        </ToggleButton>
        <SearchBar onSearch={fetchStockData} />
        {loading && <p> ...</p>}
        {error && <p style={{ color: 'red' }}>{error}</p>}
        {stockData && <StockInfo data={stockData} />}
        {graphData.length > 0 && <StockGraph data={graphData} />} {/*      */}
      </AppContainer>
    </ThemeProvider>
  );
}

export default App;
