import React from 'react';
import styled from 'styled-components';

const StockInfoContainer = styled.div`
  background-color: #4CAF50; /*   */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
`;
const Title = styled.h2`
  color: white;
  margin-top: 0;
  margin-bottom: 10px;
`;
const InfoItem = styled.p`
  color: white;
  margin: 5px 0;
`;

//   
const getWeekday = (dateString) => {
  const days = ['', '', '', '', '', '', ''];
  const date = new Date(dateString);
  return days[date.getDay()];
};

//    (YYYYMMDD -> YYYY-MM-DD)
const formatDate = (dateString) => {
  if (!dateString) {
    console.warn('formatDate     :', dateString);
    return '  ';
  }
  return `${dateString.substring(0, 4)}-${dateString.substring(4, 6)}-${dateString.substring(6, 8)}`;
};

function StockInfo({ data }) {
  if (!data) {
    return <p> .</p>;
  }

  const formattedDate = formatDate(data.basDt);
  const weekday = getWeekday(formattedDate);
  const fltRt = parseFloat(data.fltRt);
  const itmsNm = data.itmsNm; 
  const mrktCtg = parseFloat(data.mrktCtg);
  const formattedFltRt = fltRt + '%';
  console.log(data.fltRt)
  console.log(data.vs)
  console.log(data)

  // clpr vs  undefined    
  const clpr = data.clpr ? data.clpr.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') : ' ';
  const vs = data.vs ? data.vs.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') : ' ';

  return (
    <StockInfoContainer>
      <Title>  :</Title>
      <InfoItem>: {data.itmsNm}</InfoItem>
      <InfoItem>: {data.mrktCtg}</InfoItem>
      <InfoItem>: {clpr}</InfoItem>
      {/* <InfoItem>: {vs}</InfoItem> */}
      <InfoItem>: {formattedFltRt}
      </InfoItem>
      <InfoItem>: {formattedDate} ({weekday})</InfoItem>
    </StockInfoContainer>
  );
}
export default StockInfo;
