import React, { useState } from 'react';
import styled from 'styled-components';

const SearchForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
`;

const SearchInput = styled.input`
  border: none;
  background-color: #f1f1f1;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 16px;
  margin-right: 10px;
  flex-grow: 1;
`;

const DateInput = styled.input`
  border: none;
  background-color: #f1f1f1;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 16px;
  margin-top: 10px;
`;

const SearchButton = styled.button`
  border: none;
  background-color: #4CAF50;
  color: white;
  padding: 10px 15px;
  border-radius: 20px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px;

  ${(props) => props.disabled && `
    background-color: #cccccc;
    cursor: not-allowed;
  `}
`;

const WeekdayDisplay = styled.p`
  color: #333;
  font-size: 14px;
  margin-top: 5px;
`;

const getWeekday = (dateString) => {
  const days = ['', '', '', '', '', '', ''];
  const date = new Date(dateString);
  return days[date.getDay()];
};

function SearchBar({ onSearch }) {
  const [inputValue, setInputValue] = useState('SOOP');
  const [startDate, setStartDate] = useState('2024-06-03');
  const [endDate, setEndDate] = useState('2024-06-04');

  const handleSubmit = (event) => {
    event.preventDefault();
    if (inputValue.trim() === '') {
      alert(' .');
    } else {
      //       
      if (startDate && endDate) {
        onSearch(inputValue, startDate, endDate);
      } else {
        alert('     .');
      }
    }
  };

  const handleStartDateChange = (event) => {
    setStartDate(event.target.value);
  };

  const handleEndDateChange = (event) => {
    setEndDate(event.target.value);
  };

  return (
    <SearchForm onSubmit={handleSubmit}>
      <SearchInput
        type="text"
        value={inputValue}
        onChange={(e) => setInputValue(e.target.value)}
        placeholder=" ."
      />
      <DateInput
        type="date"
        value={startDate}
        onChange={handleStartDateChange}
        placeholder="  ."
      />
      <DateInput
        type="date"
        value={endDate}
        onChange={handleEndDateChange}
        placeholder="  ."
      />
      <SearchButton type="submit"></SearchButton>
    </SearchForm>
  );
}


export default SearchBar;
